@import url(https://fonts.googleapis.com/css?family=EB+Garamond);
* {
  box-sizing: border-box; }

html {
  font-size: 18px; }

body,
ul,
ol,
h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0; }

ul,
ol {
  list-style: none; }

a {
  text-decoration: none; }

input,
button {
  outline: none; }

.main {
  background-image: url(/portfolio/static/media/flowers-1230225s.20fa8dee.jpg);
  background-position: left;
  background-size: cover;
  background-attachment: fixed;
  background-origin: content-box;
  width: 100vw;
  height: 100vh; }

.container {
  height: 100vh;
  max-width: 900px;
  width: 48%;
  background-color: rgba(255, 255, 255, 0.719);
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden; }
  @media (max-width: 1199.99px) {
    .container {
      width: 60%; } }
  @media (max-width: 999.9px) {
    .container {
      width: 75%; } }
  @media (max-width: 719.9px) {
    .container {
      width: 80%; } }
  @media (max-width: 574.9px) {
    .container {
      width: 90%; } }
  .container__title {
    font-family: 'EB Garamond', serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-bottom: 30px;
    font-size: 36px;
    letter-spacing: 1px; }
    @media (max-width: 719.9px) {
      .container__title {
        font-size: 30px; } }
    @media (max-width: 574.9px) {
      .container__title {
        font-size: 24px; } }

.sites {
  padding: 10px 5%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70%;
  overflow: auto;
  flex-wrap: wrap;
  color: black;
  font-size: 18px; }
  @media (max-width: 719.9px) {
    .sites {
      padding: 10px 2%;
      justify-content: center;
      height: 60%; } }
  .sites__block {
    -webkit-transition: 0.8s;
    transition: 0.8s;
    padding: 10px 40px;
    height: 170px;
    width: 46%;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    background-color: rgba(255, 255, 255, 0.822);
    border: solid 1px white;
    margin-bottom: 10px;
    border-radius: 20px;
    box-shadow: 0px 0px 12px 0px rgba(170, 170, 170, 0.75); }
    @media (max-width: 1199.99px) {
      .sites__block {
        padding: 10px 30px; } }
    @media (max-width: 999.9px) {
      .sites__block {
        padding: 10px 20px; } }
    @media (max-width: 719.9px) {
      .sites__block {
        width: 90%;
        padding: 10px 20px; } }
    .sites__block:hover {
      -webkit-transform: scale(1.02);
              transform: scale(1.02);
      z-index: 10; }
  .sites__name {
    color: lightcoral;
    text-shadow: 1px 2px 2px #f7c4c9, -1px 2px 2px #fccacd; }
  .sites__title {
    font-family: 'EB Garamond', serif;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 25px; }
  .sites__description {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center; }
    @media (max-width: 574.9px) {
      .sites__description {
        font-size: 16px;
        width: 90%; } }
  .sites__links {
    font-size: 19px;
    width: 25%;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    @media (max-width: 719.9px) {
      .sites__links {
        width: 18%;
        font-size: 20px; } }
    @media (max-width: 574.9px) {
      .sites__links {
        width: 25%;
        font-size: 20px; } }
  .sites__link {
    color: lightcoral; }
    .sites__link:hover {
      color: black; }
  .sites__technologies {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    text-align: center; }
    @media (max-width: 574.9px) {
      .sites__technologies {
        font-size: 16px; } }
  .sites__techno {
    margin-right: 1px;
    color: lightcoral;
    font-size: 18px; }

::-webkit-scrollbar {
  width: 12px; }

::-webkit-scrollbar-thumb {
  border-width: 1px 1px 1px 2px;
  border-color: #e0e0e0;
  background-color: #e7e7e7; }

::-webkit-scrollbar-thumb:hover {
  border-width: 1px 1px 1px 2px;
  border-color: #c7c7c7;
  background-color: #c2c2c2; }

a {
  cursor: pointer; }

